














































































































$border_color: #49a0a2;
.choice-button {
  max-width: 150px;
  //background: rgb(163, 163, 228);
  margin: 10px;
  vertical-align: center;

  &__img {
    width: 100%;
    height: 150px;
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    justify-content: center;

    img {
      width: 100%;
      height: 100%;
      border-radius: 22px;
    }

    &--normal {
      transition: ease-in-out 0.2s;
      &:hover {
        background: rgba(174, 172, 169, 0.659);
        opacity: 0.5;
      }
    }
    &--active {
      border-radius: 25px;
      border: 3px solid $border_color;
      position: relative;
      &::after {
        content: "";
        position: absolute;
        top: 133px;
        left: 63px;
        color: white;
        height: 27px;
        width: 27px;
        border-radius: 50%;
        background-position: 50%;
        background-repeat: no-repeat;
        background-size: 12px;
        background-color: #49a0a2;
        background-image: url("../../../public/check-solid.svg");
      }
    }
  }
}
